<template>
    <div class="wrapper">
        <div class="title">全部歌单</div>
            <mu-flexbox class="box" wrap="wrap" justify="space-around">
                <mu-flexbox-item basis="40%" class="list-item" v-for="item in playList" :key="item.id">
                    <div class="list-bar">{{item.playCount}}</div>
                    <img class="list-img img-response" :src="item.coverImgUrl+'?param=300y300'" alt="" >
                    <div class="list-name">{{item.name}}</div>
                </mu-flexbox-item>
            </mu-flexbox>
            <mu-infinite-scroll :scroller="scroller" :loading="loading" @load="loadMore"/>
    </div>
</template>

<script>
    import api from '../api/index.js'
    export default {
        data () {
            return {
                playList:[],
                offset:0,
                loading: false,
                scroller: null
            }
        },
        mounted () {
            this.scroller = this.$el;
            this.get();
        },
        methods:{
            get(){
                this.loading = true;
                var _this=this;
                this.$http.get(api.getPlayListByWhere("全部","hot",_this.offset,true,6)).then(function(res){
                    if(res.code=200){
                        _this.playList=_this.playList.concat(res.data.playlists);
                        _this.offset=_this.offset>res.data.total?res.data.total:_this.offset+6;
                        _this.loading=false
                    }
                    else{
                        console.log(res.code);
                    }
                })
            },
            loadMore(){
               this.get();
            }
        }
    }
</script>

<style lang="less" scoped>
@import "../assets/theme.less";
.wrapper{
    height: 5.65rem;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    border: 1px solid #d9d9d9;
    padding:0 10px;
}
.img-response{
    max-width:100%;
    height: auto;
}
.title{
    margin: 5px 0 5px 0;
    padding-left: 8px;
    border-left:4px solid @primaryColor;
}
.list{
    &-item{
        margin:0 5px 5px 10px;
        position: relative;
    }
    &-bar{
        position: absolute;
        top: 0;
        left: 0;
        color: #ffffff;
        width: 100%;
        text-align: right;
        padding: 2px 5px;
        background-color:rgba(0,0,0,.2);
    }
    &-name{
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
    }
}
</style>



